import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform } from 'ionic-angular';
declare var google: any;
/**
 * Generated class for the MapPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-map',
  templateUrl: 'map.html',
})
export class MapPage {
  public map: any;
  public imageIcon: any;
  public mapstyle: any;
  public mapProp: any;
  public mapView = true;
  public flag: Boolean = false;
  public item : any; 

  constructor(public platform : Platform, public navCtrl: NavController, public navParams: NavParams) {

    this.item = navParams.data;
    //console.log(this.item);

  }

  ionViewWillEnter() {
  }

  ionViewDidLoad() {
    this.loadMap();

    ////console.log('ionViewDidLoad MapPage');
  }

  loadMap() {

    ////console.log(this.item.latitude, this.item.longitude);

    if (this.item.latitude == 0 || this.item.latitude == '128.000000') {
      //console.log('no');
      this.mapView = false;

    } else {
      //console.log('ture');
      this.mapView = true;


      this.mapstyle = [{
        featureType: "poi.business",
        elementType: "labels",
        zoomControl: true,
        fullscreenControl: true
      }];

      let LatLng = new google.maps.LatLng(this.item.latitude, this.item.longitude);

      this.mapProp = {
        streetViewControl: false,
        zoomControl: false,
        mapTypeControl: false,
        scaleControl: false,
        setMyLocationEnabled: false,
        fullscreenControl : false,
        setCompassEnabled: false,
        styles: this.mapstyle,
        zoom: 14,
        center: LatLng
      };

      var mapEle: HTMLElement = document.getElementById('mapa3');

      this.map = new google.maps.Map(mapEle,  this.mapProp );
      new google.maps.Marker({ position: LatLng, map: this.map, title: '' });
    
    }
  }


  open(){

    let destination = this.item.latitude + ',' + this.item.longitude;

      if(this.platform.is('ios')){

        window.open('maps://?q=' + destination, '_system');
        
      } else {

        let label = encodeURI('My Label');
        window.open('geo:0,0?q=' + destination + '(' + label + ')', '_system');

      }
  }



}



